import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { CardContainer, CardCategory, CardTitle } from "./";
import moment from "moment";
import "./Card.css";

export const Card = ({ blog }) => {
  const [labels, setLabels] = useState([]);
  const history = useHistory();

  const openBlog = (title, number) => {
    history.push(`/${title}/${number}`);
  };

  useEffect(() => {
    const labels = blog.labels.nodes.filter((value) => {
      return value.name !== "blog";
    });

    setLabels(labels);
  }, [blog.labels.nodes]);

  return (
    <CardContainer>
      <div onClick={() => openBlog(blog.title, blog.number)}>
        <img
          className="featuredImage"
          alt=""
          src={
            blog.title.split("#")[1] === undefined ||
            blog.title.split("#")[1] === ""
              ? "https://user-images.githubusercontent.com/16516879/80256421-51959880-869c-11ea-8618-c83fd8e25deb.jpg"
              : blog.title.split("#")[1]
          }
        />
        <div className="blogMeta">
          <CardTitle>
            {blog.title.split("#")[0]}
            <br />
            <br />
          </CardTitle>
          <div className="author">
            <div className="authorName">
              <>
                {labels.map((value, i) => {
                  return <CardCategory value={value} key={i} />;
                })}
              </>
            </div>
            <div className="blogDate">
              {moment(blog.updatedAt).format("DD MMM YYYY")}
            </div>
          </div>
        </div>
      </div>
    </CardContainer>
  );
};
